/* src/components/LoginForm.css */
body {
    margin: 0;
    padding: 0;
    background: linear-gradient(to bottom, #ffcc80, #ffffff, #aaffaa); /* Orange, White, Green */
    font-weight: bold;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; /* Align content vertically */
    min-height: 100vh; /* Set minimum height to cover the entire viewport */
  }
  
  .login-form-container {
    text-align: center;
  }

  /* Stylish heading and description */
  h1 {
    color: #3498db;
    font-size: 36px;
    margin-bottom: 10px;
  }

  p {
    color: #555;
    margin-bottom: 20px;
  }
  
  form {
    max-width: 400px;
    margin: auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  label {
    display: block;
    margin-bottom: 10px;
  }
  
  input,
  select {
    width: 100%;
    padding: 8px;
    margin-bottom: 15px;
    box-sizing: border-box;
  }
  
  button {
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 10px; /* Add space between buttons */
  }

  /* Login button styling */
  button.login {
    background-color: #800080; 
    color: white;
  }

  button.login:hover {
    background-color: #4b0082;
  }

  /* Register button styling */
  button.register {
    background-color: #3498db;
    color: white;
  }

  button.register:hover {
    background-color: #2980b9;
  }

  /* Additional space between buttons */
  button + button {
    margin-top: 0; /* Remove top margin for consecutive buttons */
  }
