/* src/components/VotingOfficerDashboard.css */
body {
    margin: 0;
    padding: 0;
    background: linear-gradient(to bottom, #ffcc80, #ffffff, #aaffaa); 
    font-weight: bold;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh; 
  }
  
  .dashboard-container {
    text-align: center;
  }
  
  h2 {
    color: #3498db;
  }
  
  p {
    color: #555;
    margin-bottom: 20px;
    font-size: 18px;
  }
  
  button {
    background-color: #e74c3c; 
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
  }
  
  button:hover {
    background-color: #c0392b; 
  }
  