/* src/pages/RegisterPage.css */
body {
  margin: 0;
  padding: 0;
  background: linear-gradient(to bottom, #ffcc80, #ffffff, #aaffaa); /* Orange, White, Green */
  font-weight: bold;
  font-size: 18px;
}

.register-page {
  text-align: center;
  max-width: 600px;
  margin: auto;
  padding: 20px;
}

h2 {
  color: #3498db;
}

p {
  color: #555;
  margin-bottom: 20px;
}

form {
  margin-top: 20px;
}

label {
  display: block;
  margin-bottom: 10px;
}

input,
select {
  width: 100%;
  padding: 4px;
  margin-bottom: 5px;
  box-sizing: border-box;
  border: 1px solid #ccc; /* Add this line to reset the border */
  border-radius: 5px;    /* Add this line to add border radius */
  outline: none;         /* Add this line to remove the outline */
}

button[type="submit"] {
  background-color: #ffd700; /* Golden color for Login button */
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  display: inline-block; /* Display buttons in one line */
  margin-right: 5px; /* Add space between buttons */
}

button[type="submit"]:hover {
  background-color: #c0c0c0; /* Darker golden color on hover for Login button */
}

/* Additional space between buttons */
button[type="submit"] + button {
  margin-top: 0; /* Remove top margin for consecutive buttons */
}

/* Update the color of the "Send OTP" button to purple */
button[type="button"]:last-child {
  background-color: #800080; /* Purple color for Send OTP button */
}

button[type="button"]:last-child:hover {
  background-color: #4b0082; /* Darker purple color on hover for Send OTP button */
}