/* src/components/VoteForm.css */
body {
    margin: 0;
    padding: 0;
    background: linear-gradient(to bottom, #ffcc80, #ffffff, #aaffaa); 
    font-weight: bold;
    font-size: 18px;
  }
  
  form {
    text-align: center;
    width: 100%;
    height: 100vh; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  label,
  button {
    display: block;
    margin-bottom: 15px;
  }
  
  select {
    width: 100%;
    padding: 8px;
    margin-bottom: 15px;
    box-sizing: border-box;
  }
  
  button {
    background-color: #3498db;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button.logout {
    background-color: #e74c3c; 
  }
  
  button:hover {
    background-color: #2980b9;
  }
  
  button + button {
    margin-top: 10px;   }
  